
@charset "UTF-8";
html, body {
  height: 100vh;
  width: 100vw;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, "Segoe UI", sans-serif;
  background-image: -webkit-gradient(linear, right top, left top, from(#00E699), to(#205AF8));
  background-image: linear-gradient(-90deg, #00E699, #205AF8);
}
main {
  margin: 18px;
}
h3 {
  margin-bottom: 0.54em;
}
h1, h2, h3, h4, h5, h6 {
  line-height: 1.5em;
  font-weight: normal;
}
p {
  line-height: 1.65em;
  text-align: justify;
}
textarea {
  width: 100%;
}
pre {
  margin-top: 1.5em;
}
ul, ol {
  padding-top: 0.4em;
  padding-bottom: 0.4em;
}
li {
  padding-top: 0.3em;
  padding-bottom: 0.3em;
}
@media screen and (max-width: 767px) {
body {
    font-size: 10pt;
}
h1, h2, h3, h4, h5, h6 {
    letter-spacing: .1em;
}
h3 {
    font-size: 18pt;
}
h4 {
    font-size: 14pt;
}
p {
    letter-spacing: .05em;
}
ul, ol {
    padding-left: 1.0em;
}
.ul_flush {
    list-style: none;
    padding-left: 0;
    display: table;
}
.ul_flush li:after {
    content: "";
    display: block;
    margin-bottom: 0.5em;
}
.ul_flush li {
    display: table-row;
}
.ul_flush li:before {
    content: "\2022";
    display: table-cell;
    padding-right: 0.4em;
}
li {
    margin-bottom: 0.5em;
}
.ol_flush {
    list-style: none;
    padding-left: 0;
    display: table;
}
.ol_flush li:after {
    content: "";
    display: block;
    margin-bottom: 0.5em;
}
.ol_flush li {
    display: table-row;
    counter-increment: table-ol;
}
.ol_flush li:before {
    content: counter(table-ol) ".";
    display: table-cell;
    padding-right: 0.4em;
    text-align: right;
}
li {
    margin-bottom: 0.5em;
}
}
@media screen and (min-width: 768px) {
body {
    font-size: 10.5pt;
}
h1, h2, h3, h4, h5, h6 {
    letter-spacing: .05em;
}
h3 {
    font-size: 18.5pt;
}
h4 {
    font-size: 16pt;
}
ul, ol {
    padding-left: 1.6em;
}
p {
    letter-spacing: .025em;
}
}
.v-enter-active {
  -webkit-transition: opacity .8s;
  transition: opacity .8s;
}
.v-enter {
  opacity: 0;
}

